import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';

/**
 * Service de base pour les appels API
 * Gre les diffrentes configurations selon le contexte (application web ou extension Chrome)
 */
@Injectable({
  providedIn: 'root'
})
export class ApiService {
  private API_URL = environment.apiUrl;
  private readonly IS_EXTENSION = environment.isExtension;
  private readonly ENV_NAME = environment.envName || 'default';

  constructor(private http: HttpClient) {
    console.log('API Service initialis avec URL:', this.API_URL);
    console.log('Contexte: ', this.IS_EXTENSION ? 'Extension Chrome' : 'Application Web');
    console.log('Environnement: ', this.ENV_NAME);
  }

  /**
   * Cre une URL complte pour une route API
   * @param endpoint Point de terminaison API (sans le prfixe /api)
   * @returns URL complte
   */
  buildApiUrl(endpoint: string): string {
    // Si l'endpoint est dj une URL complte, la retourner telle quelle
    if (endpoint.startsWith('http://') || endpoint.startsWith('https://')) {
      return endpoint;
    }
    
    // S'assurer que endpoint commence par "/" si ce n'est pas dj le cas
    if (!endpoint.startsWith('/')) {
      endpoint = '/' + endpoint;
    }
    
    // Les URL sont configures par environnement et ne devraient pas
    // ncessiter de logique conditionnelle supplmentaire ici
    // Utiliser simplement l'URL configure dans l'environnement
    
    return `${this.API_URL}${endpoint}`;
  }

  /**
   * Effectue une requte GET
   * @param endpoint Point de terminaison
   * @param options Options HTTP
   * @returns Observable de la rponse
   */
  get<T>(endpoint: string, options = {}): Observable<T> {
    return this.http.get<T>(this.buildApiUrl(endpoint), options);
  }

  /**
   * Effectue une requte POST
   * @param endpoint Point de terminaison
   * @param body Corps de la requte
   * @param options Options HTTP
   * @returns Observable de la rponse
   */
  post<T>(endpoint: string, body: any, options = {}): Observable<T> {
    return this.http.post<T>(this.buildApiUrl(endpoint), body, options);
  }

  /**
   * Effectue une requte PUT
   * @param endpoint Point de terminaison
   * @param body Corps de la requte
   * @param options Options HTTP
   * @returns Observable de la rponse
   */
  put<T>(endpoint: string, body: any, options = {}): Observable<T> {
    return this.http.put<T>(this.buildApiUrl(endpoint), body, options);
  }

  /**
   * Effectue une requte DELETE
   * @param endpoint Point de terminaison
   * @param options Options HTTP
   * @returns Observable de la rponse
   */
  delete<T>(endpoint: string, options = {}): Observable<T> {
    return this.http.delete<T>(this.buildApiUrl(endpoint), options);
  }
}