import { Injectable } from '@angular/core';
import {
  CanActivate,
  ActivatedRouteSnapshot,
  RouterStateSnapshot,
  Router
} from '@angular/router';
import { AuthService } from '../services/auth.service';
import { UserRole } from '../models/user.model';

/**
 * Garde qui vrifie si l'utilisateur est authentifi
 * Utilis pour protger les routes qui ncessitent une authentification
 */
@Injectable({
  providedIn: 'root'
})
export class AuthGuard implements CanActivate {
  constructor(private authService: AuthService, private router: Router) {}

  /**
   * Vrifie si l'utilisateur peut accder  la route
   * @param route Information sur la route active
   * @param state tat du routeur
   * @returns Boolen indiquant si l'accs est autoris
   */
  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): boolean {
    // Vrifier si l'utilisateur est authentifi
    if (!this.authService.isAuthenticated) {
      // Rediriger vers la page de connexion
      this.router.navigate(['/login'], {
        queryParams: { returnUrl: state.url }
      });
      return false;
    }

    // Si l'utilisateur doit complter l'onboarding, rediriger vers cette page (sauf si dj sur cette page)
    if (this.authService.needsOnboarding() && !state.url.includes('/onboarding')) {
      this.router.navigate(['/onboarding']);
      return false;
    }

    return true;
  }
}

/**
 * Garde qui vrifie si l'utilisateur n'est PAS authentifi
 * Utilis pour protger les routes qui ne sont accessibles qu'aux invits (login, register)
 */
@Injectable({
  providedIn: 'root'
})
export class GuestGuard implements CanActivate {
  constructor(private authService: AuthService, private router: Router) {}

  /**
   * Vrifie si l'utilisateur peut accder  la route en tant qu'invit
   * @param route Information sur la route active
   * @param state tat du routeur
   * @returns Boolen indiquant si l'accs est autoris
   */
  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): boolean {
    // Si l'utilisateur est authentifi, rediriger vers la page d'accueil
    if (this.authService.isAuthenticated) {
      // Si l'utilisateur doit complter l'onboarding, rediriger vers cette page
      if (this.authService.needsOnboarding()) {
        this.router.navigate(['/onboarding']);
      } else {
        this.router.navigate(['/']);
      }
      return false;
    }

    return true;
  }
}

/**
 * Garde qui vrifie si l'utilisateur doit complter l'onboarding
 * Utilis pour protger la route d'onboarding
 */
@Injectable({
  providedIn: 'root'
})
export class OnboardingGuard implements CanActivate {
  constructor(private authService: AuthService, private router: Router) {}

  /**
   * Vrifie si l'utilisateur doit accder  la page d'onboarding
   * @param route Information sur la route active
   * @param state tat du routeur
   * @returns Boolen indiquant si l'accs est autoris
   */
  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): boolean {
    // Vrifier si l'utilisateur est authentifi
    if (!this.authService.isAuthenticated) {
      this.router.navigate(['/login']);
      return false;
    }

    // Si l'utilisateur a dj complt l'onboarding, rediriger vers la page d'accueil
    if (!this.authService.needsOnboarding()) {
      this.router.navigate(['/']);
      return false;
    }

    return true;
  }
}

/**
 * Garde qui vrifie le rle de l'utilisateur
 * Utilis pour protger les routes qui ncessitent un rle spcifique
 */
@Injectable({
  providedIn: 'root'
})
export class RoleGuard implements CanActivate {
  constructor(private authService: AuthService, private router: Router) {}

  /**
   * Vrifie si l'utilisateur a le rle requis pour accder  la route
   * @param route Information sur la route active, contenant les rles requis
   * @param state tat du routeur
   * @returns Boolen indiquant si l'accs est autoris
   */
  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): boolean {
    // Vrifier si l'utilisateur est authentifi
    if (!this.authService.isAuthenticated) {
      this.router.navigate(['/login'], {
        queryParams: { returnUrl: state.url }
      });
      return false;
    }

    // Rcuprer les rles autoriss depuis les donnes de la route
    const allowedRoles = route.data['roles'] as UserRole[];
    if (!allowedRoles || allowedRoles.length === 0) {
      return true; // Si aucun rle n'est spcifi, l'accs est autoris
    }

    // Vrifier si l'utilisateur a l'un des rles requis
    const userRole = this.authService.getUserRole();
    if (!userRole || !allowedRoles.includes(userRole)) {
      this.router.navigate(['/forbidden']);
      return false;
    }

    return true;
  }
}