import { Component, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AuthService } from '../../../services/auth.service';
import { User } from '../../../models/user.model';

@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.css'],
  standalone: true,
  imports: [CommonModule, FormsModule, ReactiveFormsModule]
})
export class UserProfileComponent implements OnInit {
  currentUser: User | null = null;
  profileForm!: FormGroup;
  isEditing = false;
  isLoading = false;
  errorMessage: string | null = null;
  successMessage: string | null = null;

  constructor(
    private authService: AuthService,
    private router: Router,
    private fb: FormBuilder
  ) { }

  ngOnInit(): void {
    // Vrifier si l'utilisateur est connect
    if (!this.authService.isAuthenticated) {
      this.router.navigate(['/login']);
      return;
    }

    // Rcuprer l'utilisateur actuel
    this.currentUser = this.authService.currentUser;
    
    // Initialiser le formulaire avec les donnes actuelles
    this.initializeForm();
  }

  initializeForm(): void {
    if (!this.currentUser) return;

    this.profileForm = this.fb.group({
      firstName: [this.currentUser.firstName || '', [Validators.required]],
      lastName: [this.currentUser.lastName || '', [Validators.required]],
      email: [this.currentUser.email, [Validators.required, Validators.email]]
      // D'autres champs pourront tre ajouts par la suite
    });

    // Dsactiver le formulaire par dfaut (mode lecture seule)
    this.profileForm.disable();
  }

  // Activer le mode dition
  enableEditMode(): void {
    this.isEditing = true;
    this.profileForm.enable();
  }

  // Annuler les modifications
  cancelEdit(): void {
    this.isEditing = false;
    this.initializeForm(); // Rinitialiser le formulaire avec les valeurs d'origine
    this.profileForm.disable();
    this.errorMessage = null;
    this.successMessage = null;
  }

  // Soumettre les modifications
  onSubmit(): void {
    if (this.profileForm.invalid) {
      return;
    }

    this.isLoading = true;
    this.errorMessage = null;
    this.successMessage = null;

    // En environnement de dveloppement, simuler un appel API
    setTimeout(() => {
      // Simuler une mise  jour russie
      if (this.currentUser) {
        this.currentUser = {
          ...this.currentUser,
          firstName: this.profileForm.value.firstName,
          lastName: this.profileForm.value.lastName,
          email: this.profileForm.value.email
        };
      }
      
      this.isLoading = false;
      this.isEditing = false;
      this.profileForm.disable();
      this.successMessage = 'Profil mis  jour avec succs!';
    }, 1000);

    // Dans un environnement rel, nous enverrions les donnes  l'API
    /*
    this.userService.updateProfile(this.profileForm.value).subscribe({
      next: (updatedUser) => {
        this.currentUser = updatedUser;
        this.isLoading = false;
        this.isEditing = false;
        this.profileForm.disable();
        this.successMessage = 'Profil mis  jour avec succs!';
      },
      error: (error) => {
        this.isLoading = false;
        this.errorMessage = 'Une erreur est survenue lors de la mise  jour du profil.';
        console.error('Erreur lors de la mise  jour du profil:', error);
      }
    });
    */
  }
}